import {Grid,Header, Message, Segment} from 'semantic-ui-react';

export default (props) => (
    <Grid centered columns={props.columnNum}>
        <Grid.Column>
            <Header>
                {props.header}
            </Header>
            <Segment>
                {props.children}
            </Segment>
            <Message>
                {props.message}
            </Message>
        </Grid.Column>
    </Grid>
)